.page {
  font-family: "Inter", sans-serif;
  font-weight: normal;
  background-color: $color-lightPink;
  color: $color-black;
  
  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translate((-100%));
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }
}
